import React, {useState} from 'react';
import { StyleSheet, Text, View,Button } from 'react-native';
import ListItems from "./listitem"
import InputModal from "./inputmodal"

export default function App() {

  const inittodo = [{
    title:"test",
    date:"xx/xx/xxxx",
    key:"1"
  },
  {
    title:"test2",
    date:"xx/xx/xxxx",
    key:"1z"
  }
]

  const [todos, setTodos] = useState(inittodo);
  const [modalVisible, setModalVisible] = useState(false);

  const handleAddTodo = (todo) => {
    const newTodos = [...todos,todo];
    setTodos(newTodos)
    setModalVisible(false)
}

  return (
    <View style={{backgroundColor:"#E38C4C",minHeight:"100%" }}>
    <View style={{height:"30%",marginTop:30}}>
    <Text style={{fontSize:13,marginLeft:19}}>👋 Bonsoir, Nom !</Text>
    <Text style={{fontSize:20,marginTop:10,marginLeft:15}}>🔥 Prêt•e à accomplir des tâches aujourd'hui ?!</Text>
    <Text style={{fontSize:13,marginLeft:19}}>📅 Aujourd'hui nous sommes le : {new Date().toLocaleString()}</Text>

    </View>





  
    <View style={styles.container}>
      <ListItems
      todos={todos}
      setTodos={setTodos}
      />
      <InputModal
      modalVisible={modalVisible}
      setModalVisible={setModalVisible}
      handleAddTodo={handleAddTodo}
      todos={todos}
      />
</View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    borderTopRightRadius: 20,
    borderTopLeftRadius: 20,
    backgroundColor: '#FCF1DA',
    alignItems: 'center',
    justifyContent: 'center',
  },
});